<template>
  <div>
    <div id="done" v-show="done">
      <div id="doneImage"></div>
      <div class="bottomAction">
      <p>长按红色图片分享到群里</p>
      <button @click="cancel()">取消</button>
    </div>
    </div>
    <div id="doc" :class="{bg: done }">
    <div class="header">
      <p>{{title}}</p>
      <h2>2020年下垅龙胜宫庙会捐献名单</h2>
      <h6>(按捐款时间先后为序)</h6>
    </div>

    <table :class="{before: !done }" cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <th :width="done ? 30 : 20">#</th>
          <th>姓名</th>
          <th width="40" class="cash" center>现金</th>
          <th width="40" center>拱门</th>
          <th width="40" center>龙柱</th>
          <th width="40" center>气球</th>
          <th width="80" v-if="!done" center>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in list" :key="index">
          <td>{{index + 1}}</td>
          <td>{{item.name}}</td>
          <td center class="cash" :class="{novalue: done && !item.cash}">{{done ? (item.cash || '-') : item.cash}}</td>
          <td center :class="{novalue: done && !item.door}">{{done ? (item.door|| '-') : item.door}}</td>
          <td center :class="{novalue: done && !item.column}">{{done ? (item.column|| '-') : item.column}}</td>
          <td center :class="{novalue: done && !item.balloon}">{{done ? (item.balloon|| '-') : item.balloon}}</td>
          <td center v-if="!done">
            <a href="javascript: void(0);" @click="edit(index, true)">编辑</a>
            &nbsp; &nbsp; &nbsp; 
             <a href="javascript: void(0);" @click="remove(index)">删除</a>
          </td>
        </tr>

        <tr class="last">
          <td></td>
          <td><b>总计：</b></td>
          <td center class="cash"><b>{{totalCash}}</b></td>
          <td center><b>{{totalDoor}}</b></td>
          <td center><b>{{totalColumn}}</b></td>
          <td center><b>{{totalBalloon}}</b></td>
          <td center v-if="!done"></td>
        </tr>
      </tbody>
    </table>

    <div class="time" v-if="done">
      {{date}} <br>
      {{list.length > 89 ? '该名单生成工具由 Alien 提供技术支持' : ''}}
    </div>
    <div class="totalAll hide">
      总额：{{totalCash}} + 160×{{totalDoor}} + 140×{{totalColumn}} + 70×{{totalBalloon}} = <b>{{totalCount}}</b>
    </div>
    <div v-show="!done" style="height: 200px">
    </div>
  </div>

    <div class="bottomAction">
      <button @click="edit()">新增捐款</button>
      <button @click="creat()">生成截图</button>
    </div>

    <div id="new" v-show="showNew">
    <ul>
      <li>
        <b>姓名</b>
        <input type="text" v-model="newName" />
      </li>
      <li>
        <b>现金</b>
        <input type="number" v-model="newCash" />
      </li>
      <li>
        <b>拱门</b>
        <input type="number" v-model="newDoor" />
      </li>
      <li>
        <b>柱子</b>
        <input type="number" v-model="newColumn" />
      </li>
      <li>
        <b>气球</b>
        <input type="number" v-model="newBalloon" />
      </li>
    </ul>

     <div class="bottomAction">
      <button @click="save()">保存</button>
      <button @click="cancelNew()">取消</button>
    </div>
  </div>
  </div>
</template>

<script>

import html2canvas from 'html2canvas'

var Canvas2Image = function () {

	// check if support sth.
	var $support = function () {
		var canvas = document.createElement('canvas'),
			ctx = canvas.getContext('2d');

		return {
			canvas: !!ctx,
			imageData: !!ctx.getImageData,
			dataURL: !!canvas.toDataURL,
			btoa: !!window.btoa
		};
	}();

	var downloadMime = 'image/octet-stream';

	function scaleCanvas (canvas, width, height) {
		var w = canvas.width,
			h = canvas.height;
		if (width == undefined) {
			width = w;
		}
		if (height == undefined) {
			height = h;
		}

		var retCanvas = document.createElement('canvas');
		var retCtx = retCanvas.getContext('2d');
		retCanvas.width = width;
		retCanvas.height = height;
		retCtx.drawImage(canvas, 0, 0, w, h, 0, 0, width, height);
		return retCanvas;
	}

	function getDataURL (canvas, type, width, height) {
		canvas = scaleCanvas(canvas, width, height);
		return canvas.toDataURL(type);
	}

	function saveFile (strData) {
		document.location.href = strData;
	}

	function genImage(strData) {
		var img = document.createElement('img');
		img.src = strData;
		return img;
	}
	function fixType (type) {
		type = type.toLowerCase().replace(/jpg/i, 'jpeg');
		var r = type.match(/png|jpeg|bmp|gif/)[0];
		return 'image/' + r;
	}
	function encodeData (data) {
		if (!window.btoa) { throw 'btoa undefined' }
		var str = '';
		if (typeof data == 'string') {
			str = data;
		} else {
			for (var i = 0; i < data.length; i ++) {
				str += String.fromCharCode(data[i]);
			}
		}

		return btoa(str);
	}
	function getImageData (canvas) {
		var w = canvas.width,
			h = canvas.height;
		return canvas.getContext('2d').getImageData(0, 0, w, h);
	}
	function makeURI (strData, type) {
		return 'data:' + type + ';base64,' + strData;
	}


	/**
	 * create bitmap image
	 * 按照规则生成图片响应头和响应体
	 */
	var genBitmapImage = function (oData) {

		//
		// BITMAPFILEHEADER: http://msdn.microsoft.com/en-us/library/windows/desktop/dd183374(v=vs.85).aspx
		// BITMAPINFOHEADER: http://msdn.microsoft.com/en-us/library/dd183376.aspx
		//

		var biWidth  = oData.width;
		var biHeight	= oData.height;
		var biSizeImage = biWidth * biHeight * 3;
		var bfSize  = biSizeImage + 54; // total header size = 54 bytes

		//
		//  typedef struct tagBITMAPFILEHEADER {
		//  	WORD bfType;
		//  	DWORD bfSize;
		//  	WORD bfReserved1;
		//  	WORD bfReserved2;
		//  	DWORD bfOffBits;
		//  } BITMAPFILEHEADER;
		//
		var BITMAPFILEHEADER = [
			// WORD bfType -- The file type signature; must be "BM"
			0x42, 0x4D,
			// DWORD bfSize -- The size, in bytes, of the bitmap file
			bfSize & 0xff, bfSize >> 8 & 0xff, bfSize >> 16 & 0xff, bfSize >> 24 & 0xff,
			// WORD bfReserved1 -- Reserved; must be zero
			0, 0,
			// WORD bfReserved2 -- Reserved; must be zero
			0, 0,
			// DWORD bfOffBits -- The offset, in bytes, from the beginning of the BITMAPFILEHEADER structure to the bitmap bits.
			54, 0, 0, 0
		];

		//
		//  typedef struct tagBITMAPINFOHEADER {
		//  	DWORD biSize;
		//  	LONG  biWidth;
		//  	LONG  biHeight;
		//  	WORD  biPlanes;
		//  	WORD  biBitCount;
		//  	DWORD biCompression;
		//  	DWORD biSizeImage;
		//  	LONG  biXPelsPerMeter;
		//  	LONG  biYPelsPerMeter;
		//  	DWORD biClrUsed;
		//  	DWORD biClrImportant;
		//  } BITMAPINFOHEADER, *PBITMAPINFOHEADER;
		//
		var BITMAPINFOHEADER = [
			// DWORD biSize -- The number of bytes required by the structure
			40, 0, 0, 0,
			// LONG biWidth -- The width of the bitmap, in pixels
			biWidth & 0xff, biWidth >> 8 & 0xff, biWidth >> 16 & 0xff, biWidth >> 24 & 0xff,
			// LONG biHeight -- The height of the bitmap, in pixels
			biHeight & 0xff, biHeight >> 8  & 0xff, biHeight >> 16 & 0xff, biHeight >> 24 & 0xff,
			// WORD biPlanes -- The number of planes for the target device. This value must be set to 1
			1, 0,
			// WORD biBitCount -- The number of bits-per-pixel, 24 bits-per-pixel -- the bitmap
			// has a maximum of 2^24 colors (16777216, Truecolor)
			24, 0,
			// DWORD biCompression -- The type of compression, BI_RGB (code 0) -- uncompressed
			0, 0, 0, 0,
			// DWORD biSizeImage -- The size, in bytes, of the image. This may be set to zero for BI_RGB bitmaps
			biSizeImage & 0xff, biSizeImage >> 8 & 0xff, biSizeImage >> 16 & 0xff, biSizeImage >> 24 & 0xff,
			// LONG biXPelsPerMeter, unused
			0,0,0,0,
			// LONG biYPelsPerMeter, unused
			0,0,0,0,
			// DWORD biClrUsed, the number of color indexes of palette, unused
			0,0,0,0,
			// DWORD biClrImportant, unused
			0,0,0,0
		];

		var iPadding = (4 - ((biWidth * 3) % 4)) % 4;

		var aImgData = oData.data;

		var strPixelData = '';
		var biWidth4 = biWidth<<2;
		var y = biHeight;
		var fromCharCode = String.fromCharCode;

		do {
			var iOffsetY = biWidth4*(y-1);
			var strPixelRow = '';
			for (var x = 0; x < biWidth; x++) {
				var iOffsetX = x<<2;
				strPixelRow += fromCharCode(aImgData[iOffsetY+iOffsetX+2]) +
					fromCharCode(aImgData[iOffsetY+iOffsetX+1]) +
					fromCharCode(aImgData[iOffsetY+iOffsetX]);
			}

			for (var c = 0; c < iPadding; c++) {
				strPixelRow += String.fromCharCode(0);
			}

			strPixelData += strPixelRow;
		} while (--y);

		var strEncoded = encodeData(BITMAPFILEHEADER.concat(BITMAPINFOHEADER)) + encodeData(strPixelData);

		return strEncoded;
	};

	var saveAsImage = function (canvas, width, height, type) {
		if ($support.canvas && $support.dataURL) {
			if (typeof canvas == "string") { canvas = document.getElementById(canvas); }
			if (type == undefined) { type = 'png'; }
			type = fixType(type);
			if (/bmp/.test(type)) {
				var data = getImageData(scaleCanvas(canvas, width, height));
				saveFile(makeURI(genBitmapImage(data), downloadMime));
			} else {
				saveFile(getDataURL(canvas, type, width, height).replace(type, downloadMime));
			}
		}
	};

	var convertToImage = function (canvas, width, height, type) {
		if ($support.canvas && $support.dataURL) {
			if (typeof canvas == "string") { canvas = document.getElementById(canvas); }
			if (type == undefined) { type = 'png'; }
			type = fixType(type);

			if (/bmp/.test(type)) {
				var data = getImageData(scaleCanvas(canvas, width, height));
				return genImage(makeURI(genBitmapImage(data), 'image/bmp'));
			} else {
				return genImage(getDataURL(canvas, type, width, height));
			}
		}
	};



	return {
		saveAsImage: saveAsImage,
		saveAsPNG: function (canvas, width, height) {
			return saveAsImage(canvas, width, height, 'png');
		},
		saveAsJPEG: function (canvas, width, height) {
			return saveAsImage(canvas, width, height, 'jpeg');
		},
		saveAsGIF: function (canvas, width, height) {
			return saveAsImage(canvas, width, height, 'gif');
		},
		saveAsBMP: function (canvas, width, height) {
			return saveAsImage(canvas, width, height, 'bmp');
		},

		convertToImage: convertToImage,
		convertToPNG: function (canvas, width, height) {
			return convertToImage(canvas, width, height, 'png');
		},
		convertToJPEG: function (canvas, width, height) {
			return convertToImage(canvas, width, height, 'jpeg');
		},
		convertToGIF: function (canvas, width, height) {
			return convertToImage(canvas, width, height, 'gif');
		},
		convertToBMP: function (canvas, width, height) {
			return convertToImage(canvas, width, height, 'bmp');
		}
	};

}();


var initData = []

export default {
  name: 'Home',
  data () {
    return {
      title: '下垅龙胜宫，址立要穴，威灵显赫，庇佑社里四季平安、风调雨顺，近几年来，在众乡亲和众贤士的精心呵护下，龙胜宫不断完善，香火鼎盛，荫佑四方。2020年龙胜宫庙会又已临近，望众乡亲及众贤士一如既往、慷慨解囊、踊跃捐献。',
      done: false,
      totalCount: 0,
      totalCash: 0,
      totalDoor: 0,
      totalBalloon: 0,
      totalColumn: 0,
      isEdit: false,
      editIndex: 0,
      newName: '',
      newCash: '',
      newDoor: '',
      newBalloon: '',
      newColumn: '',
      showNew: false,
      list: [],
      date: ''
    }
  },
  methods: {
    creat: function() {
      this.done = true
      this.date = this.setDate()
      window.scrollTo(0, 0)
      let tagEle = document.querySelector("#doc")
      setTimeout(() => {
        html2canvas(tagEle, {
        useCORS: true,
        allowTaint: true,
        letterRendering: true,
        onrendered: function(canvas) {
        var ctx = canvas.getContext('2d');
        ctx.webkitImageSmoothingEnabled = false;
        ctx.mozImageSmoothingEnabled = false;
        ctx.imageSmoothingEnabled = false;
        },
        }).then(canvas => {
        document.querySelector("#doneImage").appendChild(Canvas2Image.convertToImage(canvas, 750, 750 * (tagEle.offsetHeight / tagEle.offsetWidth), 'png'))
      });
      }, 200);
    },
    cancel: function() {
      this.done = false
      document.querySelector("#doneImage").innerHTML = ''
    },
    setTotal: function() {
      this.totalCash = 0
      this.totalDoor = 0
      this.totalBalloon = 0
      this.totalColumn = 0
      this.list.forEach(item => {
        this.totalCash = this.totalCash + (+item.cash)
        this.totalDoor = this.totalDoor + (+item.door)
        this.totalBalloon = this.totalBalloon + (+item.balloon)
        this.totalColumn = this.totalColumn + (+item.column)
      })
      this.totalCount = this.totalCash + 160 * this.totalBalloon * 70 + this.totalColumn * 140
    },
    remove: function(index) {
      var r = confirm(`确定删除 ${this.list[index].name} 的捐款吗?`)
      if (r == true) {
        this.list.splice(index, 1)
        this.updateLocalVal()
      }  
    },
    edit: function(index, isEdit) {
      this.showNew = true
      console.log(index)
      this.isEdit = isEdit
      this.editIndex = index
      this.newName = isEdit ? this.list[index].name : ''
      this.newCash = isEdit ? this.list[index].cash : 0
      this.newDoor = isEdit ? this.list[index].door : 0
      this.newBalloon = isEdit ? this.list[index].balloon : 0
      this.newColumn = isEdit ? this.list[index].column : 0
    },
    save: function() {
      this.showNew = false
      if (!this.newName) return
      this.newCash = +this.newCash || 0
      this.newDoor = +this.newDoor || 0
      this.newBalloon = +this.newBalloon || 0
      this.newColumn = +this.newColumn || 0
      if (this.isEdit) {
        this.list[this.editIndex].name = this.newName
        this.list[this.editIndex].cash = this.newCash
        this.list[this.editIndex].door = this.newDoor
        this.list[this.editIndex].balloon = this.newBalloon
        this.list[this.editIndex].column = this.newColumn
      } else {
        this.list.push({
          name: this.newName,
          cash: this.newCash,
          door: this.newDoor,
          balloon: this.newBalloon,
          column: this.newColumn
        })
      }
      this.updateLocalVal()
    },
    updateLocalVal: function () {
      localStorage.setItem('list', JSON.stringify(this.list))
      this.setTotal()
    },
    cancelNew: function() {
      this.showNew = false
    },
    fullTwo: function(num) {
    return num > 9 ? num : '0' + num
    },
    setDate: function() {
    var now = new Date()
    var year = now.getFullYear()
    var month = this.fullTwo(now.getMonth() + 1)
    var day = this.fullTwo(now.getDate())
    var hour = this.fullTwo(now.getHours())
    var min = this.fullTwo(now.getMinutes())
    var sec = this.fullTwo(now.getSeconds())
    var bFormat = year + '-' + month + '-' + day
    var sFormat = hour + ':' + min + ':' + sec
    return bFormat + ' ' + sFormat
  },
  },
  mounted () {
    
    if (!localStorage.getItem('list')) {
      localStorage.setItem('list', JSON.stringify(initData))
      this.list = initData
    } else {
      this.list = JSON.parse(localStorage.getItem('list'))
    }

    this.setTotal()
  }
}
</script>

